import React, { useEffect } from "react";
import { NavLink } from "react-router-dom";
import API from "../services/api.service";
import "../css/todo.css";
import Swal from "sweetalert2";

function Todos() {
  const [todos, setTodos] = React.useState([]);
  const getdata = () => {
    // fetch("http://localhost:3001/todos")
    //   .then((res) => res.json())
    API.get("todo").then((data) => {
      console.log(data);
      setTodos(data.result);
    });
  };
  const edit = () => {};
  const save = () => {};
  const deleteFn = () => {};

  useEffect(() => {
    getdata();
  }, []);

  const generatedata = () => {
    const keys = Object.keys(todos[0]);
    console.log(keys);
    return keys.map((s) => <span className="ho">{s}</span>);
  };

  const d = (data) => {
    // const key = Object.keys(todos[0]);
    // return key.map((s) => {
    return <span>{data.title}</span>;
    // });
  };

  const checkChange = (data, ischecked) => {
    let newdata = {...data, completed: ischecked};
    API.put('todo/' + data._id, newdata)
    .then(data => {
      console.log(data);
      getdata();
      // Swal.fire(data.msg);
    })
  };
  
  const f = () => {
    return todos.map((s) => (
      <div>
        {/* <h1 className="text">Todo</h1> */}
        <input
          className="box"
          type="checkbox"
          checked={s.completed}
          onChange={(d) => checkChange(s, d.target.checked)}
        />
        <span>{d(s)}</span>
        <button onClick={() => edit(s)}>Edit</button>

        <button onClick={() => save(s)}>Save</button>

        <button onClick={() => deleteFn(s)}>Delete</button>
      </div>
    ));
  };
  return (
    <div className=" box">
      {todos.length > 0 ? (
        // <div className="todos">{generatedata()}</div>
        <div>{f()}</div>
      ) : (
        <p>loading...</p>
      )}
    </div>
  );
}

export default Todos;

