import React from "react";
import "../css/login.css";
import { useForm } from "react-hook-form";
import API from "../services/api.service";
import { useDispatch, useSelector } from 'react-redux'
import { login } from "../store/slice/user.slice";
import { useNavigate } from "react-router-dom";
// import login from "../assets/login.jpg";
function Login() {
  const {
    register,
    handleSubmit,
    formState: { errors },
  } = useForm();
  const dispatch = useDispatch()
  const navigate = useNavigate()
  const submit = (data) => {
    console.log(data);
    const submitData = { username: data.username, password: data.pass };

    // fetch("http://localhost:3001/users/login", {
    //   method: "POST",
    //   body: JSON.stringify(submitData),
    //   headers: {
    //     "Content-type": "application/json; charset=UTF-8",
    //   },
    // })
    //   .then((res) => res.json())
    API.post('users/login', submitData)
      .then((data) => {
        console.log(data)
        if(data.result){
          localStorage.setItem('jwt', data.result)
          dispatch(login())
          navigate('/AllComments')
        }
      });
  };
  return (
    <form className="login" onSubmit={handleSubmit(submit)}>
      <h1 className="login-heading">Login Form</h1>
      <div className="login-input">
        <input
          type="text"
          {...register("username", { required: true, minLength: 3 })}
          placeholder="Username"
        />
        {errors.username && errors.username.type === "required" && (
          <small>username is required</small>
        )}
        {errors.username && errors.username.type === "minLength" && (
          <small>username should have atleast 3 characters</small>
        )}
      </div>
      <div className="login-input">
        <input
          type="password"
          {...register("pass", { required: true })}
          placeholder="Password"
        />
        {errors.pass && errors.pass.type === "required" && (
          <small> password is required</small>
        )}
      </div>
      <div className="login-submit">
        <button className="login-button">Login</button>
      </div>
              
    </form>
  );
}
export default Login;
