import React from "react";
import "../css/signup.css";
import { useForm } from "react-hook-form";
import Swal from "sweetalert2";
import { json } from "react-router-dom";
import API from "../services/api.service";
function Signup() {
  const {
    register,
    handleSubmit,
    formState: { errors },
  } = useForm();
  const submit = (data) => {
    console.log(data);
    if (data.password === data.retypepassword) {
      const submitData = { username: data.username, password: data.password };
      // fetch("http://localhost:3001/users", {
      //   method: "POST",
      //   body: JSON.stringify(submitData),
      //   headers: {
      //     "Content-type": "application/json; charset=UTF-8",
      //   },
      // })
      //   .then((res) => res.json())
      API.post('users', submitData)
        .then((data) => console.log(data));
    } else {
      Swal.fire("Password does not match");
    }
  };

  return (
    <form className="signup" onSubmit={handleSubmit(submit)}>
      <h1 className="signup-heading">Sign-up Page</h1>
      <div className="signup-input">
        <input
          type="text"
          {...register("username", { required: true, minLength: 3 })}
          placeholder="Username"
        />
        {errors.username && errors.username.type === "required" && (
          <small>username is required</small>
        )}
        {errors.username && errors.username.type === "minLength" && (
          <small>username should have atleast 3 characters</small>
        )}
      </div>
      <div className="signup-input">
        <input
          type="password"
          {...register("password", { required: true })}
          placeholder="Password"
        />
        {errors.password && errors.password.type === "required" && (
          <small> password is required</small>
        )}
      </div>
      <div className="signup-input">
        <input
          type="password"
          {...register("retypepassword", { required: true })}
          placeholder="Re-type Password"
        />
        {errors.retypepassword && errors.retypepassword.type === "required" && (
          <small> password is required</small>
        )}
      </div>
      <div className="signup-submit">
        <button className="signup-button">Login</button>
      </div>
    </form>
  );
}
export default Signup;
