const origin = "http://localhost:3001/";

const bindorigin = (url) => origin + url;
const getHeaders = () => {
    if(localStorage.getItem('jwt')){
        return  {
            "Content-type": "application/json; charset=UTF-8",
            "Authorization": localStorage.getItem('jwt')
          } 
    } else {
        return  {
            "Content-type": "application/json; charset=UTF-8",
          }
    }
}

const API = {
  get: (url) => {
    let requrl = bindorigin(url);
    return fetch(requrl).then((res) => res.json());
  },
  post: (url, data) => {
    let requrl = bindorigin(url);
    return fetch(requrl, {
      method: "POST",
      body: JSON.stringify(data),
      headers: getHeaders(),
    }).then((response) => response.json());
  },
  put: (url, data) => {
    let requrl = bindorigin(url);
    return fetch(requrl, {
      method: "PUT",
      body: JSON.stringify(data),
      headers: getHeaders(),
    }).then((response) => response.json());
  },
  delete: (url) => {
    let requrl = bindorigin(url);
    return fetch(requrl, {
      method: "DELETE",
      headers: getHeaders(),
    }).then((response) => response.json());
  },
};
export default API;
