import React, { useEffect } from "react";
import "../css/allComments.css";
import Swal from "sweetalert2";
import API from "../services/api.service";

function AllComments() {
  const [comments, setComments] = React.useState([]);
  const [editid, seteditid] = React.useState("");
  const [editdata, seteditdata] = React.useState(null);
  const getData = () => {
    // fetch("http://localhost:3001/comments")
    // // fetch("http://192.168.1.5:3001/comments")
    //   .then((res) => res.json())
    API.get("comments").then((data) => {
      console.log(data);
      setComments(data.result);
    });
  };

  const edit = (e) => {
    // Enable for edit
    seteditdata(e);
    seteditid(e._id);
  };
  const changeeditdata = (editkey, editvalue) => {
    let newdata = { ...editdata, [editkey]: editvalue };
    console.log(newdata);
    seteditdata(newdata);
  };
  const save = (id) => {
    //save the editted data
    API.put("comments/" + editid, editdata).then((data) => {
      Swal.fire(data.msg);
      getData();
    });
    seteditid("");
    console.log(editdata);
    seteditdata(null);
  };

  const deleteFn = (id, index) => {
    // delete the data
    // console.log(id);
    fetch("http://localhost:3001/comments/" + id, {
      method: "DELETE",
    })
      .then((res) => res.json())
      .then((data) => {
        if (data.success) {
          let newComments = comments.slice();
          newComments.splice(index, 1);
          setComments(newComments);
        }
        Swal.fire(data.msg);
      });
  };

  useEffect(() => {
    getData();
  }, []);

  const generateHead = () => {
    const keys = Object.keys(comments[0]);
    console.log(keys);
    return keys.map((e) => {
      if (e !== "_id" && e !== "__v") {
        return <th>{e}</th>;
      } else {
        return <></>;
      }
    });
  };
  const generateRow = (data) => {
    const keys = Object.keys(comments[0]).filter(
      (e) => e !== "_id" && e !== "__v"
    );
    return keys.map((e) => {
      if (data._id === editid) {
        return (
          <td style={{ textAlign: "center", padding: "6px" }}>
            <input
              type="text"
              style={{ width: "95%", padding: "6px" }}
              value={editdata[e]}
              onChange={(d) => changeeditdata(e, d.target.value)}
            ></input>
          </td>
        );
      } else {
        return <td>{data[e]}</td>;
      }
    });
  };
  const generateBody = () => {
    return comments.map((e, i) => (
      <tr>
        {generateRow(e)}
        <td>
          {e._id !== editid && <button onClick={() => edit(e)}><i class="fa-solid fa-pen"></i> Edit</button>}
          {e._id === editid && (
            <button onClick={() => save(e._id)}><i class="fa-solid fa-floppy-disk"></i> Save</button>
          )}
          <button onClick={() => deleteFn(e._id, i)}><i class="fa-solid fa-trash"></i> delete</button>
        </td>
      </tr>
    ));
  };

  return (
    <>
      {comments.length > 0 ? (
        <table className="data-table">
          <thead>
            <tr>{generateHead()}</tr>
          </thead>
          <tbody>{generateBody()}</tbody>
        </table>
      ) : (
        <p>Loading...</p>
      )}
    </>
  );
}

export default AllComments;
